import React, { useState } from 'react';
import {products} from "../assets/products";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowDown } from '@fortawesome/free-solid-svg-icons';
import Modal from "../components/Modal";
import useModal from '../hooks/useModal';

export default function Products() {
    const [isOpenImageModal,showImageModal, closeImageModal] = useModal();
    const [imageProperties, setImageProperties] = useState({"label":"prueba", "img":"prueba"});
    const changeImage=(product)=>{
        setImageProperties({
            "label":product.title,
            "img": product.img
        });
        showImageModal();
    }
    return (
        <div>
            <Modal
                isOpen={isOpenImageModal}
                closeModal={closeImageModal}
                title={imageProperties.label}
            >
                <img src={imageProperties.img} alt={imageProperties.label}/>
            </Modal>
            <div className="d-flex flex-wrap justify-content-center" >
               {
                   products.map((product, index)=>(
                    <div className="card" key={index} data-aos="fade-up" data-aos-duration="1500">
                        <div>
                            <img loading="lazy" src={product.img} alt={product.title} onClick={()=>changeImage(product)} className="cursor-pointer img-opacity" />
                        </div>
                    </div>
                   ))
               }
            </div>
            <div className="d-flex justify-content-center">
                <button className="btn btn-rounded mt-1" >Contáctanos para más información <FontAwesomeIcon icon={faArrowDown}/> </button>
            </div>
        </div>
    )
}
