import './App.css';
import Footer from "./components/Footer";
import {BrowserRouter as Router ,Switch, Route} from "react-router-dom";
import Header from "./components/Header";
import Home from "./pages/Home";
import Products from "./pages/Products";
import About from "./pages/About";
import AOS from "aos";
import "aos/dist/aos.css";
AOS.init();

function App() {
  return (
    <Router>
    <div className="container d-flex flex-column justify-content-between center-one ">
     <header>
       <Header/>
     </header>
      <section className="main">
          <Switch>
            <Route path="/products">
              <Products/>
            </Route>
            <Route path="/about">
              <About/>
            </Route>
            <Route path="/">
              <Home/>
            </Route>
          </Switch>
      </section>
      <footer>
        <Footer></Footer>
      </footer>
    </div>
    </Router>
  );
}

export default App;
